import Vuex from "vuex";
import Vue from "vue";
import axios from "axios";

Vue.use(Vuex);

const dataRoot = "data/pages";
const dataLimit = 20;

export default new Vuex.Store({
  state: {
    tags: [],
    selectedTags: [],
    cards: [],
    cardsCopy: [],
    page: 1,
    loading: false,
    canBeExtended: true // Будем выставлять этот флаг каждый раз, когда натыкаемся на 404 ошибку, чтобы прекращать грузить несуществующие страницы
    // Флаг надо будет сбрасывать в true при каждом изменении списка тегов
  },
  getters: {
    isLoading: state => state.loading,
    isTagSelected: state => ({ slug }) =>
      state.selectedTags.some(tag => tag.slug === slug),
    // Так как существует проблема, то фильтрацию нужно перенести в загрузку: https://github.com/cyrillic-design/cyrillic-design.github.io/issues/1
    cards: state => state.cards
  },
  mutations: {
    setLoading(state, loading) {
      state.loading = loading;
    },
    addTag(state, tag) {
      state.selectedTags.push(tag);
    },
    removeTag(state, { slug }) {
      state.selectedTags = state.selectedTags.filter(tag => tag.slug !== slug);
    },
    setTags(state, tags) {
      state.tags = tags;
    },
    setCards(state, cards) {
      state.cards = cards;
    },
    addCards(state, cards) {
      state.cards = [...state.cards, ...cards];
    },
    nextPage(state) {
      state.page++;
    },
    setPage(state, page) {
      state.page = page;
    },
    setContinuation(state, status) {
      state.canBeExtended = status;
    }
  },
  actions: {
    selectTag({ commit, getters, dispatch }, tag) {
      if (getters.isTagSelected(tag)) {
        commit("removeTag", tag);
      } else {
        commit("addTag", { ...tag });
      }
      // При любом изменении списка тегов мы просто заново сбрасываемся на первую страницу
      // и загружаем карточки с тегами или без
      commit("setPage", 1);
      commit("setContinuation", true);
      dispatch("fetchCards");
    },
    async fetchTags({ commit }) {
      const { data } = await axios.get(`${dataRoot}/tags.json`);
      // Добавил сортировку по имени
      commit(
        "setTags",
        data.sort((a, b) =>
          a.title > b.title ? 1 : b.title > a.title ? -1 : 0
        )
      );
    },
    async fetchCards({ commit, dispatch, state }) {
      function filterCards(cards) {
        if (state.selectedTags.length <= 1) {
          return cards;
        }

        const filteredCards = [];
        for (let card of cards) {
          let hasAllTags = true;
          for (let tag of state.selectedTags) {
            if (!card.slugs.includes(tag.slug)) {
              hasAllTags = false;
            }
          }
          if (hasAllTags) {
            filteredCards.push(card);
          }
        }
        return filteredCards;
      }

      // Если уже пытались загрузить ранее, но не получилось, зачем стараться ещё раз?
      if (!state.canBeExtended) {
        return;
      }
      commit("setLoading", true);
      // Я переписал для сохранения однообразия await/async, а потом понял, что эта задержка вообще нахер не нужна
      // Ну если только по религиозным соображениям, тогда можно расскомментировать
      // await new Promise(r => setTimeout(r, 900));

      let query = `page-${state.page}`;
      // Я убрал функцию, которая отличалась от этой одной строчкой и заменил её на эту строчку
      if (state.selectedTags.length > 0) {
        query = `tags-${state.selectedTags[0].slug}-${state.page}`;
      }
      let res = null;
      // Закинемся в try/catch, чтобы обрабатывать окончание страниц
      try {
        res = await axios.get(`${dataRoot}/${query}.json`);
      } catch (err) {
        // Если мы попадаем сюда, значит данные по этому тегу/странице закончились
        // Ну и бох с ними, выключим дальнейшую загрузку
        if (404 == err.response.status) {
          commit("setContinuation", false);
        }
      }
      commit("setLoading", false);

      if (res) {
        let data = res.data;
        // Фильтрация перенесена сюда, чтобы исправить:
        // https://github.com/cyrillic-design/cyrillic-design.github.io/issues/1
        data = filterCards(data);
        if (state.page === 1) {
          commit("setCards", data);
        } else {
          commit("addCards", data);
        }
        // Если тегов не нашли, пытаемся загрузить ещё данные
        // https://github.com/cyrillic-design/cyrillic-design.github.io/issues/1
        if (data.length < dataLimit) {
          commit("nextPage");
          dispatch("fetchCards");
        }
      }
    }
  }
});
