<template>
  <div class="container is-fluid">
    <transition-group name="fade" tag="div" class="columns is-multiline">
      <div
        v-for="card in cards"
        :key="card.id"
        class="column is-full-mobile is-4-tablet is-3-desktop is-4-widescreen is-3-fullhd"
      >
        <a :href="card.url" target="_blank">
          <div class="card">
            <div class="card-image">
              <figure class="image is-16by9">
                <img v-lazy="card.image" />
              </figure>
            </div>
            <div class="card-content">
              <div class="content">
                {{ card.title }}
              </div>
              <div class="awards">
                <div
                  class="awards__award"
                  v-for="award in card.awards || []"
                  :key="award"
                >
                  <Award :type="award" />
                </div>
              </div>
            </div>
          </div>
        </a>
      </div>
    </transition-group>
    <div class="spinner" v-if="isLoading" />
    <div class="producthunt">
      <a
        href="https://www.producthunt.com/posts/cyrillic-design?utm_source=badge-featured&utm_medium=badge&utm_souce=badge-cyrillic-design"
        target="_blank"
        ><img
          src="https://api.producthunt.com/widgets/embed-image/v1/featured.svg?post_id=187146&theme=dark"
          alt="cyrillic.design - Showcase of the best Cyrillic websites with daily updates | Product Hunt Embed"
          style="width: 250px; height: 54px;"
          width="250px"
          height="54px"
      /></a>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Award from "./Award";

export default {
  components: {
    Award
  },
  computed: {
    ...mapGetters(["cards", "isLoading"])
  },
  created() {
    window.onscroll = () => {
      this.onScroll();
    };
  },
  methods: {
    onScroll() {
      if (this.isLoading) {
        return;
      }
      if (this.cards.length === 0) {
        return;
      }
      if (
        window.innerHeight + window.scrollY >=
        document.body.scrollHeight - 2
      ) {
        this.$store.commit("nextPage");
        this.loadCards();
      }
    },
    async loadCards() {
      await this.$store.dispatch("fetchCards", {
        tags: this.$store.state.selectedTags
      });
    }
  }
};
</script>

